import {h, render} from 'preact';
import { generateBoard, lookupFullWords, Board, BoardWord, consonantsToWords }  from './lib/vowelboard'
import { Provider } from 'unistore/preact'

import createStore, { ActionMap }  from 'unistore'
import { connectSafe, ActionConstructor } from './lib/storehelper'
import { LetterGrid } from './components/lettergrid';

type GameState = {
    boards: Board[],
    foundWords: Set<BoardWord>
}

let gameState = createStore<GameState>(
    { 
        boards: [1,2,3].map(_ => generateBoard(3,3,3)),
        foundWords: new Set()
    }
)

let actions: ActionConstructor<GameState> = store => ({
    foundWord: ({ foundWords }, word) => {
        return { foundWords: new Set(foundWords).add(word) }
    }
})

const App = connectSafe(['boards'], actions)(
    ({ boards }: GameState & ActionMap<GameState>) => 
       <div class="board-list">
          { boards.map(b => 
                <div class="board-row">
                    <LetterGrid board={b} selectedLetters={[]}></LetterGrid>
                    <div class="hint-container">
                    {
                        [...(new Set(b.Words).values())].map(w=> {
                            var wordTemplate = lookupFullWords(w.Word)[0].replace(/[^aeiou]/g,'_')
                            return <div class="word-hint"><span>{wordTemplate}</span></div>
                        })
                    }                    
                    </div>
                </div>
            ) }
       </div>
  )

render(
<Provider store={gameState}>
    <App />
</Provider>
, document.getElementById('root'));


/* Debug */

declare let window: any;
window['store'] = gameState;
window['answer'] = consonantsToWords;
