import { h, Component, RenderableProps } from 'preact';
import { Board }  from '../lib/vowelboard'
import { BoardPosition } from '../lib/wordboard';

interface LetterTileProps { 
    x: number, 
    y: number, 
    letter: string, 
    selected: boolean,
}

export function LetterTile( { letter, selected, x, y }: RenderableProps<LetterTileProps>) {
    var inner = selected ? <b> { letter } </b> :  letter 

    return (   
        <span class={"letter-tile" + (selected ? " selected" : "") } >
        { inner }
        </span> 
    )
}

interface LetterGridProps {
    selectedLetters: BoardPosition[]
    board: Board
    onTrySelectLetter?: (row:number, col: number, letter: string) => void
}

export class LetterGrid extends Component<LetterGridProps,{}>  {

    letters: Map<number, Component<LetterTileProps>>;

    constructor() {
        super();
        this.letters = new Map();
        this.handleMouseDrag = this.handleMouseDrag.bind(this);
        this.handleTouchDrag = this.handleTouchDrag.bind(this);
    }

    handleMouseDrag(e: MouseEvent) {
        if ((e.buttons & 1) > 0) {
            this.handlePointerDrag(e.pageX, e.pageY)
        }
        e.preventDefault();
        return false;
    }


    handleTouchDrag(e: TouchEvent) {
        this.handlePointerDrag(e.touches[0].pageX, e.touches[0].pageY)
    }

    handlePointerDrag(pageX: number, pageY: number) {
        if (!this.props.onTrySelectLetter) return;
        const eventInBounds = (r:DOMRect | ClientRect ):boolean =>pageX >= r.left && pageX < (r.left + r.width) && pageY >= r.top && pageY < (r.top + r.height);
        var letterComponent  = [...this.letters.values()].find(l=>l.base && eventInBounds(l.base.getBoundingClientRect()))
        if (!letterComponent) return;
        this.props.onTrySelectLetter(letterComponent.props.y, letterComponent.props.x, letterComponent.props.letter)
    }

    render({ board, onTrySelectLetter, selectedLetters }:LetterGridProps): JSX.Element {
        let letters = board.Letters.map( 
            (row, rownum) => row.map( 
                (letter, colnum) => (
                    <LetterTile ref={x =>this.letters.set(rownum * 1000 + colnum, x)} x={colnum} y={rownum} letter={letter}  selected={selectedLetters.find(l => l.Row == rownum && l.Col == colnum) != null }></LetterTile> 
                ) 
            )
        )

        return (
        <div class={"letter-grid cols-"+board.Cols} 
            onTouchStart={this.handleTouchDrag} 
            onTouchMove={this.handleTouchDrag} 
            onMouseDown={this.handleMouseDrag} 
            onMouseMove={this.handleMouseDrag}>
            { letters }  
        </div>
        )
    }
}
