import * as WordBoard from './wordboard'

export type Board = WordBoard.Board
export type BoardPosition = WordBoard.BoardPosition
export type WordDirection = WordBoard.WordDirection
export type BoardWord = WordBoard.BoardWord

export function stripVowels(s:string): string {
    return s.replace(/[aeiou]/g, '');
}

function mapConsonantsToWords(words: string[]): Map<string, string[]> {
    const map = new Map();
    words.forEach((item) => {
        const key = stripVowels(item);
        const collection = map.get(key);
        if (!collection) {
            map.set(key, [item]);
        } else {
            collection.push(item);
        }
    });
    return map;
}

export const fullWordset = new Set(WordBoard.commonWordList);

export const consonantsToWords = mapConsonantsToWords(WordBoard.commonWordList);

export function generateBoard(width: number, height: number, minConsonantLength: number): Board {
    return WordBoard.genBoard([...consonantsToWords.keys()].filter(x=>x.length >= minConsonantLength), width, height);
}

export function lookupFullWords(voweless: string): string[] {
    return consonantsToWords.get(voweless) || []
}