import { Store, ActionCreator, StateMapper } from 'unistore'
import { connect } from 'unistore/preact'

type ActionFn<K> = (state: K, ...args: any[]) => Partial<K>;

interface ActionMap<K> {
	[actionName: string]: ActionFn<K>;
}

export type ActionConstructor<K> = (store: Store<K>) => ActionMap<K>

export function connectSafe<T, K, I>(
    mapStateToProps: string | Array<keyof K> | StateMapper<T, K, I>,
    actions?: ActionCreator<K> | object
): (
    Child: ((props?: T & I) => JSX.Element) | preact.ComponentConstructor<T & I> | preact.AnyComponent<T & I>
) => preact.ComponentConstructor<T> {
    return connect(mapStateToProps as string | Array<string> | StateMapper<T, K, I> , actions);
}